"use client"

import { useEffect, useState, Suspense } from "react"
import { useParams, useRouter } from "next/navigation"
import dynamic from "next/dynamic"
import { motion } from "framer-motion"
import { Button } from "@/components/ui/button"
import CommentsSection from "@/components/comments-section"
import ReactionsBar from "@/components/reactions-bar"
import ReviewsSection from "@/components/reviews-section"
import { 
  Star, 
  BookOpen, 
  ShoppingCart, 
  ChevronRight, 
  Sparkles, 
  Bookmark, 
  Share2,
  Eye,
  Clock
} from "lucide-react"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Card, CardContent } from "@/components/ui/card"

// Dynamically import PdfViewer
const PdfViewer = dynamic(() => import("@/components/pdf-viewer"), {
  ssr: false,
  loading: () => (
    <div className="flex items-center justify-center h-96">
      <div className="text-center">
        <div className="w-16 h-16 border-4 border-primary/20 border-t-primary rounded-full animate-spin mx-auto mb-4"></div>
        <p className="text-muted-foreground">Loading PDF viewer...</p>
      </div>
    </div>
  ),
})

interface Book {
  _id: string
  title: string
  description: string
  price: number
  type: string
  cover: string
  isbn: string
  filePath?: string
  stock?: number
  author?: string
  pages?: number
  language?: string
  publishedDate?: string
  category?: string[]
}

export default function BookDetailPage() {
  const params = useParams()
  const router = useRouter()
  const id = params.id as string
  const [book, setBook] = useState<Book | null>(null)
  const [loading, setLoading] = useState(true)
  const [isAddingToCart, setIsAddingToCart] = useState(false)

  useEffect(() => {
    async function fetchBook() {
      try {
        const res = await fetch(`/api/books/${id}`)
        if (res.ok) {
          const data = await res.json()
          setBook(data)
        }
      } catch (error) {
        console.error("Failed to fetch book:", error)
      } finally {
        setLoading(false)
      }
    }
    fetchBook()
  }, [id])

  const handleAddToCart = async () => {
    if (!book) return
    setIsAddingToCart(true)
    try {
      const res = await fetch("/api/cart", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          bookId: id,
          quantity: 1,
          price: book.price,
        }),
      })
      if (res.ok) {
        // Success animation
        setTimeout(() => {
          router.push("/cart")
        }, 800)
      } else {
        alert("Please login to add items to cart")
      }
    } catch (error) {
      console.error("Failed to add to cart:", error)
    } finally {
      setIsAddingToCart(false)
    }
  }

  if (loading) return <BookDetailSkeleton />
  if (!book) return <BookNotFound />

  return (
    <main className="min-h-screen bg-gradient-to-b from-background to-muted/30 py-8 px-4 sm:px-6 lg:px-8">
      {/* Floating decorative elements */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none -z-10">
        <div className="absolute top-20 left-10 w-72 h-72 bg-primary/5 rounded-full blur-3xl"></div>
        <div className="absolute bottom-20 right-10 w-96 h-96 bg-secondary/5 rounded-full blur-3xl"></div>
        <div className="absolute top-1/2 left-1/4 w-64 h-64 bg-accent/5 rounded-full blur-3xl"></div>
      </div>

      <motion.div 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="max-w-7xl mx-auto"
      >
        {/* Breadcrumb */}
        <nav className="mb-8">
          <motion.div 
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            className="flex items-center gap-2 text-sm text-muted-foreground"
          >
            <button 
              onClick={() => router.push("/")}
              className="hover:text-foreground transition-colors"
            >
              Home
            </button>
            <ChevronRight className="w-4 h-4" />
            <button 
              onClick={() => router.push("/books")}
              className="hover:text-foreground transition-colors"
            >
              Books
            </button>
            <ChevronRight className="w-4 h-4" />
            <span className="text-foreground font-medium truncate">{book.title}</span>
          </motion.div>
        </nav>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Left Column - Book Cover & Basic Info */}
          <motion.div 
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5, delay: 0.1 }}
            className="lg:col-span-1 space-y-6"
          >
            <Card className="overflow-hidden border-0 shadow-lg bg-gradient-to-br from-card to-card/80 backdrop-blur-sm">
              <CardContent className="p-6">
                {/* Book Cover with Glow Effect */}
                <div className="relative group mb-6">
                  <div className="absolute -inset-4 bg-gradient-to-r from-primary/20 to-secondary/20 rounded-2xl blur opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
                  <div className="relative aspect-[3/4] bg-gradient-to-br from-muted to-muted/50 rounded-xl overflow-hidden shadow-2xl transform group-hover:-translate-y-1 transition-transform duration-300">
                    {book.cover ? (
                      <img 
                        src={book.cover || "/placeholder.svg"} 
                        alt={book.title}
                        className="w-full h-full object-cover"
                        onLoad={(e) => {
                          e.currentTarget.classList.add('loaded')
                        }}
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-primary/10 to-secondary/10">
                        <BookOpen className="w-16 h-16 text-primary/40" />
                      </div>
                    )}
                  </div>
                  <div className="absolute top-4 right-4">
                    <Badge className="bg-gradient-to-r from-primary to-primary/80 text-white px-3 py-1">
                      {book.type === "physical" ? "Physical" : "E-book"}
                    </Badge>
                  </div>
                </div>

                {/* Book Stats */}
                <div className="grid grid-cols-2 gap-4 mb-6">
                  <div className="text-center p-3 bg-muted/50 rounded-lg">
                    <div className="text-2xl font-bold text-primary">
                      {book.price}
                    </div>
                    <div className="text-xs text-muted-foreground">Price</div>
                  </div>
                  <div className="text-center p-3 bg-muted/50 rounded-lg">
                    <div className="text-2xl font-bold text-primary">
                      {book.stock || "‚àû"}
                    </div>
                    <div className="text-xs text-muted-foreground">Stock</div>
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="space-y-3">
                  <Button 
                    onClick={handleAddToCart}
                    disabled={isAddingToCart}
                    className="w-full bg-gradient-to-r from-primary to-primary/90 hover:from-primary/90 hover:to-primary shadow-lg hover:shadow-xl transform hover:-translate-y-0.5 transition-all duration-300"
                  >
                    {isAddingToCart ? (
                      <>
                        <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin mr-2"></div>
                        Adding...
                      </>
                    ) : (
                      <>
                        <ShoppingCart className="mr-2 h-4 w-4" />
                        Add to Cart
                      </>
                    )}
                  </Button>
                  
                  <div className="flex gap-2">
                    <Button variant="outline" className="flex-1">
                      <Bookmark className="mr-2 h-4 w-4" />
                      Save
                    </Button>
                    <Button variant="outline" className="flex-1">
                      <Share2 className="mr-2 h-4 w-4" />
                      Share
                    </Button>
                  </div>
                </div>

                {/* Quick Info */}
                <div className="mt-6 space-y-3 pt-6 border-t">
                  <div className="flex items-center gap-2 text-sm">
                    <BookOpen className="w-4 h-4 text-muted-foreground" />
                    <span className="text-muted-foreground">ISBN:</span>
                    <span className="font-medium">{book.isbn}</span>
                  </div>
                  {book.category && (
                    <div className="flex flex-wrap gap-2 mt-3">
                      {book.category.slice(0, 3).map((cat) => (
                        <Badge key={cat} variant="secondary" className="text-xs">
                          {cat}
                        </Badge>
                      ))}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </motion.div>

          {/* Right Column - Details */}
          <motion.div 
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="lg:col-span-2 space-y-8"
          >
            {/* Book Title & Description */}
            <Card className="border-0 shadow-lg bg-gradient-to-br from-card to-card/80 backdrop-blur-sm overflow-hidden">
              <CardContent className="p-8">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <motion.h1 
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ duration: 0.5, delay: 0.3 }}
                      className="text-4xl lg:text-5xl font-bold bg-gradient-to-r from-foreground to-foreground/80 bg-clip-text text-transparent mb-2"
                    >
                      {book.title}
                    </motion.h1>
                    {book.author && (
                      <p className="text-lg text-muted-foreground mb-4">
                        By <span className="text-primary font-medium">{book.author}</span>
                      </p>
                    )}
                  </div>
                  <div className="flex items-center gap-1">
                    {[...Array(5)].map((_, i) => (
                      <Star key={i} className="w-5 h-5 fill-yellow-400 text-yellow-400" />
                    ))}
                    <span className="ml-2 text-sm text-muted-foreground">(4.8)</span>
                  </div>
                </div>

                <motion.div 
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ duration: 0.5, delay: 0.4 }}
                  className="prose prose-lg max-w-none"
                >
                  <p className="text-foreground/90 leading-relaxed">{book.description}</p>
                </motion.div>

                {/* Book Details Grid */}
                <motion.div 
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.5 }}
                  className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-8 pt-8 border-t"
                >
                  <DetailItem icon={<BookOpen />} label="Type" value={book.type} />
                  <DetailItem icon={<Eye />} label="Pages" value={book.pages || "N/A"} />
                  <DetailItem icon={<Clock />} label="Language" value={book.language || "English"} />
                  <DetailItem icon={<Sparkles />} label="Published" value={book.publishedDate || "N/A"} />
                </motion.div>
              </CardContent>
            </Card>

            {/* Embedded PDF Viewer */}
            {book.type !== "physical" && book.filePath && (
              <motion.div 
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.6 }}
                className="overflow-hidden rounded-2xl shadow-2xl border-0"
              >
                <div className="bg-gradient-to-r from-primary/10 to-secondary/10 p-6">
                  <div className="flex items-center gap-3 mb-4">
                    <div className="p-2 bg-primary/20 rounded-lg">
                      <BookOpen className="w-6 h-6 text-primary" />
                    </div>
                    <h2 className="text-2xl font-bold bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
                      Read Now
                    </h2>
                    <div className="ml-auto">
                      <Sparkles className="w-5 h-5 text-yellow-500 animate-pulse" />
                    </div>
                  </div>
                  <Suspense fallback={
                    <div className="flex items-center justify-center h-64 bg-gradient-to-br from-card to-card/50 rounded-xl">
                      <div className="text-center">
                        <div className="w-12 h-12 border-3 border-primary/30 border-t-primary rounded-full animate-spin mx-auto mb-4"></div>
                        <p className="text-muted-foreground">Preparing your reading experience...</p>
                      </div>
                    </div>
                  }>
                    <PdfViewer fileUrl={book.filePath} />
                  </Suspense>
                </div>
              </motion.div>
            )}

            {/* Interactions */}
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.7 }}
            >
              <ReactionsBar contentId={id} contentType="book" />
            </motion.div>

            {/* Reviews Section */}
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.8 }}
            >
              <ReviewsSection contentId={id} contentType="book" />
            </motion.div>

            {/* Comments Section */}
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.9 }}
            >
              <CommentsSection contentId={id} contentType="book" />
            </motion.div>
          </motion.div>
        </div>
      </motion.div>
    </main>
  )
}

function DetailItem({ icon, label, value }: { icon: React.ReactNode; label: string; value: string | number }) {
  return (
    <div className="text-center p-4 bg-gradient-to-br from-muted/50 to-muted/20 rounded-xl hover:from-muted/70 hover:to-muted/40 transition-all duration-300">
      <div className="flex justify-center mb-2 text-primary">{icon}</div>
      <div className="text-xs text-muted-foreground mb-1">{label}</div>
      <div className="font-semibold">{value}</div>
    </div>
  )
}

function BookDetailSkeleton() {
  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/30 py-8 px-4">
      <div className="max-w-7xl mx-auto">
        {/* Skeleton breadcrumb */}
        <div className="flex items-center gap-2 mb-8">
          <Skeleton className="h-4 w-20" />
          <Skeleton className="h-4 w-4" />
          <Skeleton className="h-4 w-24" />
          <Skeleton className="h-4 w-4" />
          <Skeleton className="h-4 w-32" />
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Left column skeleton */}
          <div className="space-y-6">
            <Card className="border-0 shadow-lg">
              <CardContent className="p-6">
                <Skeleton className="aspect-[3/4] w-full rounded-xl mb-6" />
                <div className="grid grid-cols-2 gap-4 mb-6">
                  <Skeleton className="h-20 rounded-lg" />
                  <Skeleton className="h-20 rounded-lg" />
                </div>
                <Skeleton className="h-10 w-full rounded-lg mb-3" />
                <div className="flex gap-2">
                  <Skeleton className="h-10 flex-1 rounded-lg" />
                  <Skeleton className="h-10 flex-1 rounded-lg" />
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right column skeleton */}
          <div className="lg:col-span-2 space-y-8">
            <Card className="border-0 shadow-lg">
              <CardContent className="p-8">
                <Skeleton className="h-12 w-3/4 mb-4" />
                <Skeleton className="h-6 w-1/2 mb-6" />
                <div className="space-y-3 mb-8">
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-4 w-2/3" />
                </div>
                <div className="grid grid-cols-4 gap-4">
                  {[...Array(4)].map((_, i) => (
                    <Skeleton key={i} className="h-20 rounded-xl" />
                  ))}
                </div>
              </CardContent>
            </Card>

            {[...Array(3)].map((_, i) => (
              <Skeleton key={i} className="h-64 rounded-2xl" />
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

function BookNotFound() {
  const router = useRouter()
  
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="min-h-screen flex items-center justify-center bg-gradient-to-br from-background to-muted/30 px-4"
    >
      <Card className="max-w-md w-full border-0 shadow-2xl bg-gradient-to-br from-card to-card/80 backdrop-blur-sm">
        <CardContent className="p-8 text-center">
          <div className="w-24 h-24 mx-auto mb-6 bg-gradient-to-br from-destructive/10 to-destructive/20 rounded-full flex items-center justify-center">
            <BookOpen className="w-12 h-12 text-destructive" />
          </div>
          <h2 className="text-3xl font-bold mb-4">Book Not Found</h2>
          <p className="text-muted-foreground mb-8">
            The book you're looking for doesn't exist or has been removed.
          </p>
          <Button 
            onClick={() => router.push("/books")}
            className="bg-gradient-to-r from-primary to-primary/90 hover:from-primary/90 hover:to-primary"
          >
            Browse Books
          </Button>
        </CardContent>
      </Card>
    </motion.div>
  )
}